import io from 'socket.io-client';
import xss from 'xss';
var socket = io();
var wcounter = 0;
console.log("Worker working!");
socket.on('disconnect', function () { console.log('Socket.IO disconnected!'); });
var worker = self;
self.addEventListener("message", function (e) {
    var type = e.data[0];
});
// start socket server
var getToken = function () {
    socket.emit('get_mapbox_token', {});
    //    console.log('Get mapbox token')
};
socket.on('connect', function () {
    console.log('Socket.IO connected!');
    worker.postMessage({ type: 'CONNECTED' });
    setTimeout(getToken, 500); // 500msec after send get=mapbox-token
});
socket.on('mapbox_token', function (payload) {
    //    console.log('token Got:' + payload)
    worker.postMessage({
        type: 'RECEIVED_MAPBOX_TOKEN',
        payload: payload
    });
    // this
    if (wcounter === 0) {
        startRecivedData();
        wcounter++; // assign only once.
    }
});
//this.getPitch.bind(this))
//socket.on('bearing', this.getBearing.bind(this))
//socket.on('clearMoves', this.getClearMoves.bind(this))
var toArrayColor = function (color) {
    return [
        ((color & 0xFF0000) >> 16),
        ((color & 0x00FF00) >> 8),
        (color & 0x0000FF)
    ];
};
var createGradientColorGenerator = function (minValue, maxValue) {
    var min = minValue !== null && minValue !== void 0 ? minValue : 0;
    var max = maxValue !== null && maxValue !== void 0 ? maxValue : 0;
    var basegreen = 0;
    var basered = 255;
    var ratio = 255 / (max - min);
    if (ratio < 0) {
        var rt_1 = -ratio;
        return function (value) {
            var v = value ? (value < max ? max : (value > min ? min : value)) : 0;
            var green = basegreen + rt_1 * (v - max);
            var red = basered - rt_1 * (v - max);
            return [Math.floor(red), Math.floor(green), 0];
        };
    }
    else {
        return function (value) {
            var v = value ? (value > max ? max : (value < min ? min : value)) : 0;
            var green = basegreen + ratio * (v - min);
            var red = basered - ratio * (v - min);
            return [Math.floor(red), Math.floor(green), 0];
        };
    }
};
var getData = function (bar) {
    var time = bar.ts.seconds;
    var colorType = bar.colorType;
    var shapeType = bar.shapeType;
    var isVarColor = colorType === 1;
    var isHexa = shapeType === 1;
    var colorGenerator = createGradientColorGenerator(bar.min, bar.max);
    //    console.log('time is ' + time)
    var r = {
        id: bar.id,
        movesbaseidx: bar.id,
        sourcePosition: [],
        sourceColor: [],
        targetPosition: [],
        targetColor: [],
        elapsedtime: time,
        position: [bar.lon, bar.lat, 0],
        angle: 0,
        speed: 0,
        shapeType: isHexa ? 6 : 4,
        areaColor: toArrayColor(bar.color),
        data: bar.barData.map(function (b) {
            return {
                value: b.value,
                color: isVarColor ? colorGenerator(b.value) : toArrayColor(b.color),
                label: b.label,
            };
        }),
        radius: bar.radius,
        width: bar.width,
        min: bar.min,
        max: bar.max,
        text: bar.text,
    };
    //    console.log("BarData:",r)
    return r;
};
function color2array(col) {
    return [(col >>> 16) % 256, (col >>> 8) % 256, col % 256];
}
function startRecivedData() {
    var xssFilter = new xss.FilterXSS({});
    socket.on('bargraphs', function (str) {
        console.log('Bargraphs:' + str.length);
        var rawData = JSON.parse(str);
        var bars = rawData.bars;
        var payload = bars.map(function (b) { return getData(b); });
        worker.postMessage({
            type: 'RECEIVED_BAR_GRAPHS',
            payload: payload
        });
    });
    socket.on('agents', function (str) {
        var payload = JSON.parse(str);
        //        console.log('Agents:' + str.length)
        worker.postMessage({
            type: 'RECEIVED_AGENT',
            payload: payload
        });
    });
    socket.on('pitch', function (str) {
        //        console.log('Pitch:' + str)
        var payload = JSON.parse(str).pitch;
        if (payload == undefined) {
            payload = 0;
        }
        worker.postMessage({
            type: 'RECEIVED_PITCH',
            payload: payload
        });
    });
    socket.on('bearing', function (str) {
        //        console.log('Bearing:' + payload)
        var payload = JSON.parse(str).bearing;
        if (payload == undefined) {
            payload = 0;
        }
        worker.postMessage({
            type: 'RECEIVED_BEARING',
            payload: payload
        });
    });
    socket.on('clearMoves', function (payload) {
        console.log('ClearMoves:' + payload);
        worker.postMessage({
            type: 'RECEIVED_CLEAR_MOVES',
            payload: payload
        });
    });
    socket.on('lines', function (str) {
        var payload = JSON.parse(str);
        //        wcounter ++
        //        console.log('lines:' + payload.length+","+wcounter)        
        worker.postMessage({
            type: 'RECEIVED_LINES',
            payload: payload
        });
    });
    socket.on('geojson', function (payload) {
        //        wcounter ++
        //        console.log('geoJSON:' + payload.length+","+wcounter)
        worker.postMessage({
            type: 'RECEIVED_GEOJSON',
            payload: payload
        });
    });
    socket.on('viewstate', function (payload) {
        //        console.log('viewstate:' + payload)
        //        wcounter ++
        //        console.log('setViewstate:' + payload +","+wcounter)
        worker.postMessage({
            type: 'RECEIVED_VIEWSTATE',
            payload: payload
        });
    });
    socket.on('arcs', function (payload) {
        console.log("Receive Arcs!:" + payload);
        var data = JSON.parse(payload);
        var arcs = [];
        for (var i = 0; i < data.srcs.length; i++) {
            arcs.push({
                src: [data.srcs[i].lon, data.srcs[i].lat],
                tgt: [data.tgts[i].lon, data.tgts[i].lat],
                srcCol: color2array(data.srcCols[i]),
                tgtCol: color2array(data.tgtCols[i]),
                tilt: data.tilts[i]
            });
        }
        worker.postMessage({
            type: 'RECEIVED_ARCS',
            payload: arcs
        });
    });
    socket.on('clearArcs', function (payload) {
        worker.postMessage({
            type: 'RECEIVED_CLEAR_ARCS',
            payload: payload
        });
    });
    socket.on('scatters', function (payload) {
        console.log("Receive Scatters!:" + payload);
        var data = JSON.parse(payload);
        var scatters = [];
        for (var i = 0; i < data.points.length; i++) {
            scatters.push({
                pos: [data.points[i].lon, data.points[i].lat],
                radius: data.radiuses[i],
                fillCol: color2array(data.fillColors[i]),
                lineCol: color2array(data.lineColors[i]),
                lineWid: 2
            });
        }
        worker.postMessage({
            type: 'RECEIVED_SCATTERS',
            payload: scatters
        });
    });
    socket.on('clearScatters', function (payload) {
        worker.postMessage({
            type: 'RECEIVED_CLEAR_SCATTERS',
            payload: payload
        });
    });
    socket.on('topLabelInfo', function (payload) {
        console.log("Receive LabelInfo!:" + payload);
        var data = JSON.parse(payload);
        // we have to check CSS! (using validator) here
        var labelInfo = {
            label: xssFilter.process(data.label),
            style: data.style
        };
        worker.postMessage({
            type: 'RECEIVED_LABEL_INFO',
            payload: labelInfo
        });
    });
}
